.wizard hr {
    margin: 10px 5px;
    border: 1px solid rgb(190, 190, 190, 0.5);
}

.wizard-header {
    padding: 24px 24px 0 24px;
    font-family: 'Rock Salt', 'Roboto', Arial, Helvetica, sans-serif;
}

.wizard-body {
    padding: 0 24px;
    min-height: 300px;
}

.wizard-buttons {
    display: grid;
    grid-template-columns: 1fr 2fr 1fr 1fr;
    column-gap: 18px;
    padding: 0 24px 24px 24px;
}

.wizard-buttons .cancel-button {
    grid-column: 1 / span 1;
}

.wizard-buttons .back-button {
    grid-column: 3 / span 1;
}

.wizard-buttons .next-button {
    grid-column: 4 / span 1;
}

.wizard-buttons .submit-button {
    grid-column: 4 / span 1;
}

